@import 'colors';

.ui-container {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;

  img {
    width: 350px;
    height: auto;
  }

  .text-pourcent {
    font-size: 62px;
    color: white;
  }
}

.btn {
  border-radius: 15px;
  border: 1px solid $beige;
  padding: 10px 25px;
  background: transparent;
  color: $beige;
  text-decoration: none;
}

// SLIDER TEXT
.slider-container-ui {
  position: absolute;
  width: 100%;
  height: 100%;

  .nextButton,
  .prevButton {
    position: absolute;
    top: 45vh;
    padding: 25px;
    font-size: 46pt;
    color: white;
    cursor: pointer;
  }

  .nextButton {
    right: 20px;
  }
}

// ALIGN
.align-center {
  margin: 0 auto;
  display: block;
  text-align: center;
  width: max-content;
  left: 0;
  right: 0
}

.vertical-center {
  position: absolute;
  top: 40vh;
}
